import { useLocation } from "react-router-dom";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import {
  useAutomateInstitutePlateformStreamSubjectMasterList,
  useAutomateInstituteStreamSubjectMaster,
} from "../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import StreamSubjectMasterCard from "./StreamSubjectMasterCard";
import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../../Hooks/ApiHooks/Auth/auth-api";
const StreamSubjectMaster = () => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteStreamSubjectMaster] =
    useAutomateInstituteStreamSubjectMaster();

  const {
    automateInstitutePlateformStreamSubjectMasterList,
    automateInstitutePlateformStreamSubjectMasterListLoading,
  } = useAutomateInstitutePlateformStreamSubjectMasterList({
    data: {
      stid: getQuery.state?.stream?._id,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.stream?._id,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstitutePlateformStreamSubjectMasterList?.list?.length) {
      let obj = nextPage(
        automateInstitutePlateformStreamSubjectMasterList?.list?.length
      );
      setState(() => obj);
    }
  }, [automateInstitutePlateformStreamSubjectMasterList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && getQuery.state?.stream?._id) {
            automateInstituteStreamSubjectMaster({
              stid: getQuery.state?.stream?._id,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <div className={style.fm_header_container}>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <UploadExcelButton
            onExcelFileUpload={onExcelFileUpload}
            label={t("subject_excel")}
            customStyle={{
              width: "10rem",
            }}
          />
        </div>
      </div>
      {automateInstitutePlateformStreamSubjectMasterList?.list?.map(
        (subject, index) =>
          automateInstitutePlateformStreamSubjectMasterList?.list?.length ===
          index + 1 ? (
            <div key={subject?._id} ref={ref}>
              <StreamSubjectMasterCard subject={subject} />
            </div>
          ) : (
            <StreamSubjectMasterCard subject={subject} key={subject?._id} />
          )
      )}
      {automateInstitutePlateformStreamSubjectMasterListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        automateInstitutePlateformStreamSubjectMasterList?.list?.length ===
          0 && (
          <EmptyMessage
            title="No subjects found related this search."
            customStyleContainer={{
              marginTop: "10rem",
            }}
          />
        )
      ) : (
        automateInstitutePlateformStreamSubjectMasterList?.list?.length ===
          0 && (
          <EmptyMessage
            title="No subjects."
            customStyleContainer={{
              marginTop: "10rem",
            }}
          />
        )
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StreamSubjectMaster;
