import { useLocation, useParams, useSearchParams } from "react-router-dom";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import CustomSquareTabCard from "../../Ui/Tab/Square/CustomSquareTabCard";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";

const AutomateInstituteProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  let measure = document.getElementById("562nbhgh43")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <UserMemberTabsWrapper>
      <SquareTabWrapper dynamicId="562nbhgh43">
        <CustomSquareTabCard
          label="institute_type"
          iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
          url={`/q/${params.username}/automate/institute?type=institute`}
          state={getQuery.state}
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
        <CustomSquareTabCard
          label="affiliated_with"
          iconUrl={`${assestsUserFinanceUrl}/pending.svg`}
          url={`/q/${params.username}/automate/institute?type=university`}
          state={getQuery.state}
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
        <CustomSquareTabCard
          label="department_type"
          iconUrl={`${assestsUserFinanceUrl}/pending.svg`}
          url={`/q/${params.username}/automate/institute?type=department`}
          state={getQuery.state}
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />

        <CustomSquareTabCard
          label="all_stream"
          iconUrl={`${assestsUserAdmissionUrl}/payment-verification.svg`}
          url={`/q/${params.username}/automate/institute?type=stream`}
          state={getQuery.state}
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
      </SquareTabWrapper>
    </UserMemberTabsWrapper>
  );
};

export default AutomateInstituteProfile;
