import HorizontalTabWrapper from "../../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { useTranslation } from "react-i18next";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import SubjectTheoryTeachingPlan from "./SubjectTheoryTeachingPlan";
import { Suspense } from "react";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import SubjectTutorialTeachingPlan from "./SubjectTutorialTeachingPlan";
import SubjectPracticalTeachingPlan from "./SubjectPracticalTeachingPlan";

const SubjectMasterTeachingPlan = () => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();

  return (
    <>
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={`/q/${params.username}/automate/institute?type=subject${tab.urlKey}`}
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />

      <Suspense fallback={<QvipleLoader />}>
        {searchParams.get("n") === "tutorial" ? (
          <SubjectTutorialTeachingPlan smid={getQuery.state?.subject?._id} />
        ) : searchParams.get("n") === "practical" ? (
          <SubjectPracticalTeachingPlan smid={getQuery.state?.subject?._id} />
        ) : (
          <SubjectTheoryTeachingPlan smid={getQuery.state?.subject?._id} />
        )}
      </Suspense>
    </>
  );
};

export default SubjectMasterTeachingPlan;

const tabList = [
  {
    label: "theory",
    id: "theory",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "tutorial",
    id: "tutorial",
    urlKey: "&n=tutorial",
    groupIs: ["tutorial"],
  },
  {
    label: "practical",
    id: "practical",
    urlKey: "&n=practical",
    groupIs: ["practical"],
  },
];
